import React from 'react'
import MultiplePizzaz from  "../assets/multiplepizzas.jpg"
import "../styles/About.css"

function About() {
  return (
    <div className='about'>
        <div className="aboutTop" style={{ backgroundImage: `url(${MultiplePizzaz})`}}></div>
        <div className="aboutBottom">
        <h2>About Us</h2>
      <p>
        Welcome to Piiizah, your ultimate destination for mouthwatering and delicious pizzas! We are passionate about crafting the perfect pizza using only the finest ingredients, from our homemade dough to our signature sauce and a wide range of fresh toppings. Our team of talented chefs combines traditional recipes with innovative flavors to create a pizza experience that will leave you craving for more.
      </p>
      <p>
        At Piiizah, we believe that every pizza should be a masterpiece. That's why we take great care in selecting the highest quality ingredients and pay attention to every detail of the pizza-making process. Whether you're a fan of classic Margherita, a meat lover, or a vegetarian, we have a variety of options to satisfy all taste buds.
      </p>
      <p>
        Beyond our delicious pizzas, we strive to provide exceptional customer service. Our friendly and knowledgeable staff is here to ensure that your dining experience at Piiizah is nothing short of amazing. We also offer convenient online ordering, speedy delivery, and a cozy dine-in atmosphere for you to enjoy our pizzas with family and friends.
      </p>
      <p>
        Thank you for choosing Piiizah. We are excited to serve you the best pizza in town and create memories that will last a lifetime. Come and experience the irresistible taste of our pizzas today!
      </p>
        </div>
    </div>
  )
}

export default About
