import React from 'react';
import Logo from "../assets/pizza-logo.jpg";
import { Link } from 'react-router-dom';
// import ReorderIcon from '@mui/icons-material/Reorder';
import "../styles/Navbar.css";
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/js/bootstrap.bundle';



function Navbar() {



  return (
    <div>
    
      <nav className="navbar navbar-expand-lg navbar-light bg-dark">
      <div className='container'>
        <img src={Logo} alt="Logo" />
        <button className="navbar-toggler " type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav ms-auto" id='navtop'>
            <li className="nav-item">
              <Link to='/' className='nav-link' style={{color:'rgb(243 197 8)'}}>Home</Link>
            </li>
            <li className="nav-item">
              <Link to='/menu' className='nav-link' style={{color:'rgb(243 197 8)'}} >Menu</Link>
            </li>
            <li className="nav-item">
              <Link to='/about' className='nav-link' style={{color:'rgb(243 197 8)'}}>About</Link>
            </li>
            <li className="nav-item">
              <Link to='/contact' className='nav-link' style={{color:'rgb(243 197 8)'}}>Contact</Link>
            </li>
          </ul>
        </div>
        </div>
      </nav>
    </div>
  );
}

export default Navbar;
